.kewwordsCon{
    margin-top: 50px;
}
.kewwordsCon>h1{
    text-align: center;
}
.keyWordsDiv{      
    width: 100%;
    max-width: 1000px;
    margin: 0px auto;
    padding: 20px;
    line-height: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.keyWordsDiv>span{
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.362);
    color: black;
    margin: 10px;
    border-radius: 10px;
}